<template>
    <div class="full_div">

        <div id="background" :style="this.backgroundStyle">

          <div class="md-layout" id="overlay_background">
          </div>

        </div>


      <transition name="slide-fade">
        <div id="text_div">
          <div id="title_background">
            <span><div class="red_character">Software </div> Engineer and Architect </span>
          </div>

          <div class="gap_div">
          </div>

          <div id="sub_title_background">
            Build <span class="red_character"> scalable</span> architecture
          </div>
        </div>
      </transition>

    </div>

</template>

<script>

  export default {

    name: 'background',
    data(){
        return {
            backgroundStyle: {}
        }
    },
    mounted() {

      let lFollowX = 0,
        lFollowY = 0,
        x = 0,
        y = 0,
        friction = 1 / 40;

      let moveBackground = () => {
        x += (lFollowX - x) * friction;
        y += (lFollowY - y) * friction;

        let translate = 'translate(' + x + 'px, ' + y + 'px) scale(1.05)';

        this.backgroundStyle = {
          '-webit-transform': translate,
          '-moz-transform': translate,
          'transform': translate
        };

        window.requestAnimationFrame(moveBackground);
      };

      window.addEventListener('mousemove', function(e) {

        const lMouseX = Math.max(-100, Math.min(100, window.innerHeight / 2 - e.clientX));
        const lMouseY = Math.max(-100, Math.min(100, window.innerWidth / 2 - e.clientY));
        lFollowX = (20 * lMouseX) / 100; // 100 : 12 = lMouxeX : lFollow
        lFollowY = (10 * lMouseY) / 100;

      });

      moveBackground();

    }

  }

</script>

<style scoped="">
  .full_div {
      width: 100%;
      height: 100%;
  }

  .red_character {
    color: #F20505;
    font-size: 4.5rem;
    animation: bounce 1s infinite alternate;
    -webkit-animation: bounce 1s infinite alternate;
  }

  @keyframes bounce {
    from {
      transform: translateY(0px);
    }
    to {
      transform: translateY(-15px);
    }
  }
  @-webkit-keyframes bounce {
    from {
      transform: translateY(0px);
    }
    to {
      transform: translateY(-15px);
    }
  }


  #background {

    /* Full height */
    height: 100%;

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #000000;
  }

  #overlay_background {
    height: 100%;
    background-color: #000;
    opacity: 0.5;
  }

  #title_background {

    font-size: 4rem;
    font-family: 'Open Sans', sans-serif;
    color: white;
    line-height: 1.2em;
    display: inline-block;

  }

  #sub_title_background {
    font-size: 1.5rem;
    font-family: 'Open Sans', sans-serif;
    color: white;
    line-height: 1.2em;
    display: inline-block;
  }

  #text_div {
    position: absolute;
    top: 40%;
    left: 20%;
  }

  .gap_div{
    height: 30px;
  }

  /* Enter and leave animations can use different */
  /* durations and timing functions.              */
  .slide-fade-enter-active {
    transition: all 1.5s ease;
  }
  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active below version 2.1.8 */ {
    transform: translateX(100px);
    opacity: 0;
  }


</style>



