import Vue from 'vue'
import Router from 'vue-router'

import MainView from '@/components/MainView'
import TravelBlog from '@/components/TravelBlog'
import NavigationDrawer from '@/components/NavigationDrawer'
import LombokPage from '@/components/travel_blog_page/LombokPage'
import PastProjects from '@/components/past_projects/PastProjects'
import AboutMyself from '@/components/about_myself/AboutMyself'
import TechBlog from '@/components/tech_blog/TechBlog'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mainView',
      component: MainView
    },
    {
      path: '/nav',
      name: 'navigationDrawer',
      component: NavigationDrawer
    },
    {
      path: '/travelBlog',
      name: 'travelBlog',
      component: TravelBlog
    },
    {
      path: '/lombokPage',
      name: 'lombokPage',
      component: LombokPage
    },
    {
      path: '/pastProjects',
      name: 'pastProjects',
      component: PastProjects
    },
    {
      path: '/aboutMyself',
      name: 'aboutMyself',
      component: AboutMyself
    },
    {
      path: '/techBlog',
      name: 'techBlog',
      component:TechBlog
    },

  ]
})



