<template>

  <div class="full_div">

    <div class="md-layout full_div">
      <div class="md-layout-item" id="first_half">
        <div id="overlay_black">
        </div>
      </div>

      <div class="md-layout-item">

          <div id="second_half" class="full_div">

            <div id="close_icon">
              <md-button class="md-icon-button" v-on:click="handleClose">
                <md-icon>close</md-icon>
              </md-button>
            </div>

            <div class="margin_div">
            </div>

            <div class="menu_div" v-on:click="handlePastProjects">
                Past Projects
            </div>

            <div class="margin_div">
            </div>

            <div class="menu_div" v-on:click="handleTechBlog">
                Tech Blog
            </div>

            <div class="margin_div">
            </div>

            <div class="menu_div" v-on:click="handleTravelBlog">
                Travel Blog
              </div>

            <div class="margin_div">
            </div>

            <div class="small_menu_div" v-on:click="handleAboutMyself">
              About myself
            </div>
          </div>

      </div>

    </div>

  </div>

</template>

<script>

  import router from "@/router"

  export default {
      name: "navigationDrawer",

      methods: {
        handleClose: function() {
          router.push("/");
        },

        handleTravelBlog: function() {
            router.push("/travelBlog");
        },

        handlePastProjects: function() {
            router.push("/pastProjects");
        },

        handleTechBlog: function() {
          router.push("/techBlog");
        },

        handleAboutMyself: function() {
            router.push("/aboutMyself");
        }
      },

      data() {
          return {
          };
      }

  }

</script>


<style scoped>

  .full_div {
      width: 100%;
      height: 100%;
  }

  .menu_div {
      font-size: 4rem;
      cursor: pointer;
  }

  .menu_div:hover {
      color: #F20505;
  }

  .small_menu_div {
      font-size: 1rem;
      cursor: pointer;
  }

  .small_menu_div:hover {
      color: #F20505;
  }

  .margin_div {
      height: 10%;
  }

  #first_half {

    /* Full height */
    height: 100%;

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image:url("../assets/past_projects/background.jpg");

  }

  #overlay_black {
      width: 100%;
      height: 100%;
      background-color: #000;
      opacity: 0.9;
  }

  #second_half {
    text-align: center;
  }

  #close_icon {
      text-align: right;
  }


</style>



